@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.AirportSelectMenu {
    border: 1px solid rgb(151, 26, 53);
    border-radius: 20px;
    padding: 2px;
    margin-top: 5px;
    border-width: 1px;
    animation: fade-in 1s;
    animation-delay: 0.5;
    max-width: 40%;
}